.preloader {
  position: relative;
  background-color: #fff;
  width: 100%;
  height: 100px;
  .item {
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    margin: auto;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    animation: preloaderRotate 1.4s linear infinite;
    .item-child {
      position: absolute;
      width: 15px;
      height: 15px;
      background-color: #222;
      border-radius: 50%;
      margin: auto;
      top: -15px;
      left: 0;
      right: 0;
      animation: preloaderScale 1.4s linear infinite;
    }
  }
  .item:nth-child(2),
  .item:nth-child(2) .item-child {
    animation-delay: .7s;
  }
}

@-webkit-keyframes preloaderRotate {
  0% {
    opacity: 1;
    -webkit-transform: rotate(0);
  }
  100% {
    opacity: 0.6;
    -webkit-transform: rotate(360deg);
  }
}

@-moz-keyframes preloaderRotate {
  0% {
    opacity: 1;
    -moz-transform: rotate(0);
  }
  100% {
    opacity: 0.6;
    -moz-transform: rotate(360deg);
  }
}

@keyframes preloaderRotate {
  0% {
    opacity: 1;
    transform: rotate(0);
  }
  100% {
    opacity: 0.6;
    transform: rotate(360deg);
  }
}

@-webkit-keyframes preloaderScale {
  0% {-webkit-transform: scale(0);}
  35% {-webkit-transform: scale(1.4);}
  50% {-webkit-transform: scale(1);}
  65% {-webkit-transform: scale(1.4);}
  100% {-webkit-transform: scale(0);}
}

@-moz-keyframes preloaderScale {
  0% {-moz-transform: scale(0);}
  35% {-moz-transform: scale(1.4);}
  50% {-moz-transform: scale(1);}
  65% {-moz-transform: scale(1.4);}
  100% {-moz-transform: scale(0);}
}

@keyframes preloaderScale {
  0% {transform: scale(0);}
  35% {transform: scale(1.4);}
  50% {transform: scale(1);}
  65% {transform: scale(1.4);}
  100% {transform: scale(0);}
}
