.modal-wrapper {
  position: relative;
  height: 500px;
}

.modal-bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 500px;
  background: url("https://www.placecage.com/c/500/250") no-repeat center center;
  background-size: cover;
}

.modal {
  position: absolute;
  width: 100%;
  height: 500px;
  background-color: rgba(255, 255, 255, .875);
}

.modal ul {
  position: absolute;
  top: 50%;
  left: 50%;
  display: block;
  width: auto;
  font-size: 30px;
  list-style: none;
  line-height: 60px;
  height: 60px;
  padding-left: 0;
  padding-right: 0;
  transform: translate(-50%, -50%);
  li {
    display: inline-block;
    width: auto;
    padding: 0 28px;
    cursor: pointer;
    a {
      color: $brand-secondary;
    }
  }
}
