#header-page {
  background-color: $brand-primary;
  box-shadow: 1px 1px 3px rgba(0,0,0,.25);
  width: 100%;
  height: 80px;
  .container {
    position: relative;
    height: 80px;
  }
  .logo {
    position: absolute;
    top: 50%;
    left: 15px;
    width: 120px;
    height: 46px;
    overflow: hidden;
    transform: translateY(-50%);
    a {
      display: block;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
}

.navigation {
  font-family: $secondary-font;
  font-weight: 400;
  text-align: right;
  .menu-list {
    display: none;
    font-size: 0;
    list-style: none;
    padding: 0;
    margin: 0;
    li {
      position: relative;
      display: inline-block;
      width: auto;
      a {
        display: block;
        font-family: $secondary-font;
        font-weight: 400;
        font-size: 10px;
        color: $brand-white;
        background: none;
        line-height: 80px;
        padding: 0 20px;
        text-transform: uppercase;
        letter-spacing: 0.2em;
        .caret {
          display: inline-block;
          width: 0;
          height: 0;
          margin-left: 2px;
          vertical-align: middle;
          border-top: 4px dashed;
          border-top: 4px solid\9;
          border-right: 4px solid transparent;
          border-left: 4px solid transparent;
        }
      }
      &.active a {
        color: $brand-secondary;
      }
    }
    @include bp-lg {
      display: inline-block;
    }
  }
}

.open-menu-responsive {
  position: absolute;
  display: block;
  width: 20px;
  height: 60px;
  cursor: pointer;
  top: 50%;
  right: 0;
  overflow: hidden;
  z-index: 99;
  transform: translateY(-50%);
  .hamburger {
    position: absolute;
    width: 20px;
    height: 14px;
    margin: auto;
    top: 0;
    bottom: 0;
    left: 0;
    .item {
      position: absolute;
      display: block;
      font-size: 0;
      width: 20px;
      height: 2px;
      background-color: #fff;
      margin: auto;
      left: 0;
      right: 0;
      overflow: hidden;
      z-index: 1;
      &.item-1 {
        top: 0;
      }
      &.item-2 {
        top: 0;
        bottom: 0;
      }
      &.item-3 {
        bottom: 0;
      }
    }
  }
  @include bp-lg {
    display: none;
  }
}
