.action-bar {
  list-style: none;
	margin: 0;
	padding: 0;
  li {
    display: block;
  }
  a {
  	color: #e7e7e7;
  	display: block;
  	font-size: 32px;
  	height: 64px;
  	line-height: 64px;
  	position: relative;
  	text-align: center;
  	@include transition(.3s, background, ease);
  	width: 64px;
    background-color: $brand-black;
    &:hover {
      background-color: $brand-secondary;
    }
  }
}
