.textfield,
.select{
  background-color: $brand-white;
  display: block;
  margin-top: -2px;
  margin-bottom: 2.4rem;
  position: relative;
}

.textfield__label,
.select__label{
  font-size: rem(12px);
  position: absolute;
  left: 1px;
  line-height: 1;
  padding: .5rem;
  right: 1px;
  top: 1px;
}

.textfield input,
.textfield textarea,
.select select {
  background-color: transparent;
  border: 1px solid lighten($brand-gray, 25%);
  border-radius: 2px;
  box-sizing: border-box;
  padding: 1.8rem .5rem .6rem;
  position: relative;
  width: 100%;
  z-index: 10;
}

.textfield *:focus,
.select select:focus{
  border-color: lighten($brand-gray, 25%);
  box-shadow: 0 1px 2px 0 lighten($brand-gray, 45%) inset;
  outline: none;
}

.textfield,
.select {
  *[disabled]{
    background-color: lighten($brand-gray, 45%);
    color: $brand-gray;
  }
}
