.progress {
  height: 20px;
  margin-bottom: 20px;
  overflow: hidden;
  background-color: #f5f5f5;
  border-radius: 4px;
  box-shadow: inset 0 1px 2px rgba(0,0,0,.1);
}

.progress-bar {
  float: left;
  width: 0;
  height: 100%;
  font-size: 12px;
  line-height: 20px;
  color: $brand-white;
  text-align: center;
  background-color: $brand-secondary;
  box-shadow: inset 0 -1px 0 rgba(0,0,0,.15);
  @include transition(.6s, width, ease);
}
