.switch-box {
  position: relative;
  overflow: hidden;
  text-align: left;
  font-weight: 700;
  width: 78px;
  padding: 5px;
  background: #f7f7f7;
  border: solid 1px #eee;
  border-radius: 50em;

	input {
		position: absolute;
		left: -9999em;
	}

	.option {
    cursor: pointer;
    display: block;
    background: none;
    position: relative;
    padding: 0;
    min-height: 0;
    width: 200px;
    left: -35px;
    transition: all ease-in-out .25s;
	}

	.active-state, .inactive-state {
		text-transform: uppercase;
		padding: 0 5px;
		vertical-align: middle;
		font-size: 15px;
		display: inline-block;
	}

  .inactive-state {
    padding-left: 5px;
  }

	.icon {
		vertical-align: middle;
		width: 23px;
		height: 23px;
		background: #a94442;
		border-radius: 50em;
		display: inline-block;
		transition: all ease-in-out .25s;
	}

	input:checked ~ .option {
		left: 0;

		.icon {
			background: #3c7641;
		}

	}

}
